.ourWork {
  width: 84vw;
  margin: 140px auto 0;
  &__title {
   width: 100%;
   text-align: center;
   font-weight: 700;
   font-size: 50px;
   line-height: 50px;
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
    max-width: 1230px;
    margin: 0 auto;


    &-block {
      width: 588px;
      height: 554px;
      box-shadow: 0px 8px 15px 0px #BBA98E26;
      border-radius: 15px;
      display: flex;
      flex-direction: column;
      margin-bottom: 25px;
      transition: transform 0.3s ease-in-out;
      &:hover {
        transform: scale(1.05);
      }
      &:nth-child(even) {
        margin-left: 25px;
      }

      &-title {
        font-size: 32px;
        line-height: 30px;
        text-align: center;
        margin-top: 45px;

      }
      &-txt {
        color: $black;
        height: 60px;
        max-width: 413px;
        margin: 0 auto;
        font-weight: 400;
        line-height: 20px;
        font-size: 16px;
        text-align: center;
      }
      &-img {
        height: 244px;
        width: auto;
        margin: 0 auto;

      }
      &-button {
        width: 107px;
        height: 45px;
        border: 1px solid $blue;
        border-radius: 20px;
        color: $blue;
        font-weight: 700;
        cursor: pointer;
        font-size: 18px;
        line-height: 18px;
        background-color: $white;
        transition: background-color 0.3s ease;
        margin: 0 auto 50px;
        &:hover {
          background-color: $blue;
          color: $white;
        }
      }
    }
  }
  &__content-block:nth-child(1) {
    color: #A9BC87;
  }
  &__content-block:nth-child(2) {
    color: #8BB5C9;
  }
  &__content-block:nth-child(3) {
    color: #00A0B6;
  }
  &__content-block:nth-child(4) {
    color: #8B60D3;
  }

  &__content-block-img:nth-child(1) {
    margin-top: 25px;
    margin-bottom: 30px;

  }
  &__content-block-img:nth-child(2) {
    margin-bottom: 25px;
    margin-top: 50px;

  }
  &__content-block-img:nth-child(3) {
    margin-top: 30px;
    margin-bottom: 25px;

  }
  &__content-block-img:nth-child(4) {
      margin-top: 11px;
      margin-bottom: 44px;

  }
}


@media screen and (max-width: 1430px) and (min-width: 550px) {
  .ourWork__content {
    justify-content: space-around;
    &-block:nth-child(even) {
      margin-left: 0;
    }
  }
}

@media screen and (max-width: 550px) {
  .ourWork__content {
    justify-content: space-around;
    &-block:nth-child(even) {
      margin-left: 0;
    }
  }


  .ourWork {
    margin: 100px auto 0;
    &__title {
     font-size: 40px;
     line-height: 40px;
    }
    &__content {
      &-block {
        width: 260px;
        height: 370px;

        &:nth-child(even) {
          margin-left: 0;
        }

        &-title {
          font-size: 26px;
          line-height: 24px;
        }
        &-txt {
          max-width: 250px;
          line-height: 14px;
          font-size: 12px;
        }
        &-img {
          height: 108px;
        }
        &-button {
          width: 80px;
          height: 40px;
          font-size: 14px;
          line-height: 14px;
          margin: 0 auto 30px;
        }
      }
    }
  }
}
